.navigation {
    position: fixed;
    width: 100vw;
    height: -webkit-fill-available;

    .menu-icon {
        position: absolute;
        right: 1rem;
        top: 1rem;
        height: 2rem;
        width: 2rem;
        overflow: hidden;
        cursor: pointer;

        .fa-bars,
        .fa-times {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            font-size: 2rem;
        }
    }

    .active {
        position: absolute;
        height: 100vh;
    }
}